<template>
  <div v-if="!isShow" class="loading">
    <img src="../../assets/img/timg.gif" alt />
  </div>
  <div v-else>
    <a-player :audio="audio" :lrcType="3"  class="musicPlayer" ref="aplayer"></a-player>
    <table class="music-list">
      <thead>
        <tr>
          <th></th>
          <th>歌曲标题</th>
          <th>歌手</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in musicList" :key="item.id" @click="clickHandle(item.id)">
          <td>{{index+1}}</td>
          <td>{{item.name}}</td>
          <td>{{item.ar[0].name}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      audio: {
        name: "东西（Cover：林俊呈）",
        artist: "纳豆",
        url: "https://cdn.moefe.org/music/mp3/thing.mp3",
        cover: 'https://p1.music.126.net/5zs7IvmLv7KahY3BFzUmrg==/109951163635241613.jpg?param=300y300', // prettier-ignore
         },
      isShow: false,
      musicList: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let urlapi = `https://bird.ioliu.cn/netease/playlist?id=947524264`;
      axios.get(urlapi).then(res => {
        this.musicList = res.data.playlist.tracks;
        // console.log(res.data.playlist);
        this.isShow = true;
      });
    },
    clickHandle(id) {
      let thisUrl = "https://bird.ioliu.cn/netease/song?id=" + id;
      axios.get(thisUrl).then(res => {
        console.log(res);
        let obj = {
          name: res.data.data.name,
          artist: res.data.data.ar[0].name,
          url: res.data.data.mp3.url,
          cover: res.data.data.al.picUrl
        };
        // this.audio.name = res.data.data.name
        this.audio = obj;
        // console.log(res.data.data.name);
        this.$refs.aplayer.play();
        
      });
    }
  }
};
</script>
<style scoped>
/* table,
tr {
  width: 100vw;
} */
table {
  display: inline-block;
  margin-top: 2.3rem;
}
thead th {
  font-size: 0.5rem;
}
tbody td {
  font-size: 16px;
  margin: 20px 0;
  text-align: center;
}

.clearfix::before {
  content: "";
  display: block;
  clear: both;
}
.music-list {
  border-collapse: collapse;
}
.music-list tbody tr:nth-child(odd) {
  background-color: pink;
}
.loading {
  width: 100vw;
}
.loading img {
  width: 100%;
}
.musicPlayer {
  position: fixed;
  right: 0;
  left: 0
}
</style>
